limits = function() {
  var i, len, loc, max_latitude, max_longitude, min_latitude, min_longitude;
  max_longitude = -180;
  min_longitude = 180;
  max_latitude = -90;
  min_latitude = 90;
  for (i = 0, len = locations.length; i < len; i++) {
    loc = locations[i];
    if (loc.longitude < min_longitude) {
      min_longitude = loc.longitude;
    }
    if (loc.longitude > max_longitude) {
      max_longitude = loc.longitude;
    }
    if (loc.latitude < min_latitude) {
      min_latitude = loc.latitude;
    }
    if (loc.latitude > max_latitude) {
      max_latitude = loc.latitude;
    }
  }
  return {
    max_long: max_longitude,
    min_long: min_longitude,
    max_lat: max_latitude,
    min_lat: min_latitude
  };
};

loadMap = function() {
  var map, mapElement, menu_btn;
  mapElement = d3.select('.map');
  map = new Datamap({
    element: mapElement[0][0],
    scope: 'world',
    responsive: true,
    geographyConfig: {
      popupOnHover: false,
      highlightOnHover: false,
      borderWidth: 0
    },
    bubblesConfig: {
      borderWidth: 0,
      fillOpacity: 1,
      highlightFillColor: '#FFFFFF',
      highlightBorderWidth: 2,
      highlightBorderColor: '#000000'
    },
    fills: {
      'cultural': cultural_blue,
      'living': living_work_red,
      defaultFill: '#e9eaea'
    },
    data: {
      'cultural': {
        fillKey: 'cultural'
      },
      'living': {
        fillKey: 'living'
      }
    }
  });
  map.bubbles(locations, {
    popupTemplate: function(geo, data) {
      return "<div class=\"hoverinfo\"> " + data.description + " <br/> " + data.city + ", " + data.country + " <br/>On " + data.date + " </div>";
    }
  });
  menu_btn = d3.select('.content .menu-btn');
  return menu_btn.on('click.map', function(evt) {
    return window.setTimeout(function() {
      return map.resize();
    }, 300);
  });
};

reloadMap = function() {
  var mapElement;
  mapElement = d3.select('.map');
  mapElement[0][0].innerHTML = '';
  return loadMap();
};

var registerToggleMenu;

registerToggleMenu = function() {
  var menu_btn, toggle_menu;
  toggle_menu = function(evt) {
    var menu;
    menu = d3.select('.menu');
    if (menu.classed('hidden')) {
      return menu.classed('hidden', false);
    } else {
      return menu.classed('hidden', true);
    }
  };
  menu_btn = d3.select('.content .menu-btn');
  return menu_btn.on('click.menu', toggle_menu);
};

var languages_data, loadRadials, loadSingleRadial, reloadRadials, reloadSingleRadial, renderRadial;

languages_data = [
  {
    name: 'brazil',
    percent: 100
  }, {
    name: 'italy',
    percent: 87.5
  }, {
    name: 'england',
    percent: 75
  }, {
    name: 'spain',
    percent: 75
  }
];

renderRadial = function(radial, container, img) {
  var img_size_factor, new_flag_left, new_flag_top, new_image_dimension, size;
  size = radial.diameter(container.clientWidth + 26).render().getSize();
  img_size_factor = 0.9;
  new_image_dimension = size.width / 2 * img_size_factor;
  img.style.width = '' + new_image_dimension + 'px';
  img.style.display = 'block';
  new_flag_left = size.width / 2 - (new_image_dimension / 2) + 1;
  new_flag_top = size.width / 2 - (img.clientHeight / 2) + 1;
  img.style.top = '' + new_flag_top + 'px';
  return img.style.left = '' + new_flag_left + 'px';
};

loadSingleRadial = function(elem, index) {
  var div, img, radial;
  img = elem.children[0];
  div = elem.children[1];
  radial = radialProgress(div).value(languages_data[index].percent);
  renderRadial(radial, div, img);
  return radial;
};

loadRadials = function() {
  var elem, i, index, len, ref, results;
  ref = (d3.selectAll('.languages-container .radial'))[0];
  results = [];
  for (index = i = 0, len = ref.length; i < len; index = ++i) {
    elem = ref[index];
    results.push(loadSingleRadial(elem, index));
  }
  return results;
};

reloadSingleRadial = function(radial, elem, index) {
  var div, img;
  img = elem.children[0];
  div = elem.children[1];
  div.innerHTML = '';
  return renderRadial(radial, div, img);
};

reloadRadials = function(radials) {
  var elem, i, index, len, ref, results;
  ref = (d3.selectAll('.languages-container .radial'))[0];
  results = [];
  for (index = i = 0, len = ref.length; i < len; index = ++i) {
    elem = ref[index];
    results.push(reloadSingleRadial(radials[index], elem, index));
  }
  return results;
};

var radials;

radials = [];

window.onload = function(evt) {
  registerToggleMenu();
  radials = loadRadials();
  return loadMap();
};

window.onresize = function(evt) {
  reloadRadials(radials);
  return reloadMap();
};
